@use "colors";
@use "base";

//Menu
.menu {
  background-color: colors.$color-wood-light;
  position: fixed;
  top: 72px;
  height: auto;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @media screen and (min-width: 540px) {
    top: 39.5px;
    flex-direction: row-reverse;
  }
}
.menu__list {
  display: none;
  list-style: none;
  margin: 0;
  padding: 0;

  @media screen and (min-width: 540px) {
    display: flex;
    justify-content: flex-end;
  }
}

//Toggler - hamburger menu
.menu__toggler {
  font-size: 30px;
  display: block;
  cursor: pointer;
  padding-left: 15px;

  @media screen and (min-width: 540px) {
    display: none;
  }
}

.menu__checkbox {
  position: absolute;
  left: -9999px;
}

.menu__checkbox:checked ~ .menu__list {
  display: block;
}

//Menu links
.menu__link {
  display: block;
  padding: 15px;
  color: colors.$color-black;
  text-decoration: none;
  font-size: 20px;
  text-transform: lowercase;
  font-weight: bold;

  @media screen and (min-width: 540px) {
    padding: 5px 15px;
  }
}

.menu__link:hover,
.menu__link:active,
.menu__link:focus {
  color: colors.$color-wood-light2;
}

.menu__link--active {
  color: colors.$color-wood-light2;
}

//Dropdown menu
.menu__dropdown {
  display: none;

  @media screen and (min-width: 540px) {
    list-style-type: none;
    position: fixed;
    top: 80px;
    right: 135px;
    padding: 0;
    margin: 0;
    background-color: colors.$color-wood-light;
    font-weight: bold;
  }
}

.drop-menu-item {
  @media screen and (min-width: 540px) {
    padding: 2px 15px;
    cursor: pointer;
  }
}

.drop-menu-link:hover,
.drop-menu-link:focus,
.drop-menu-link:hover {
  @media screen and (min-width: 540px) {
    color: colors.$color-wood-light2;
  }
}

.menu__logo {
  display: none;
  width: 60px;
  position: absolute;
  right: 0px;

  @media screen and (min-width: 540px) {
    left: 0px;
    width: 80px;
  }

  @media screen and (min-width: 960px) {
    width: 130px;
    filter: drop-shadow(0.5px 0.5px 0.5px white);
  }
}
