@use "colors";
@use "base";

//Footer
.footer {
  background-color: colors.$color-wood-light3;
  margin: 0;
  text-align: center;
  line-height: 1.3;

  @media screen and (min-width: 540px) {
    display: grid;
    grid-template-columns: 1.5fr 2fr;
    grid-template-rows: 1fr auto;
  }

  @media screen and (min-width: 596px) {
    grid-template-columns: 1fr 2fr;
  }
}

//Footer headline
.footer__headline3 {
  margin-top: 18px;
  line-height: 1;
  font-size: 30px;
}

//Footer section contact
.footer__contact {
  padding: base.$spacer;
}

//Footer map
.footer__map {
  width: 100%;
  height: 300px;
  margin: 0;

  @media screen and (min-width: 540px) {
    height: 100%;
  }
}

//Footer e-mail
.footer__email {
  color: #fff8dc;
  text-decoration: none;
}

.footer__email:active,
.footer__email:focus,
.footer__email:hover {
  color: colors.$color-wood-light2;
  text-decoration: underline;
}

//Footer copyright
.footer__copyright {
  padding: 15px;
  margin: 0;
  background-color: colors.$color-wood-light;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;

  @media screen and (min-width: 540px) {
    grid-column: 1 / -1;
  }
}

.footer__copyright p {
  margin: 0;
}

//Footer tabulka provozní doba
.footer__table {
  text-align: left;
  max-width: auto;
  margin: 0 auto;
}

.footer__table td {
  padding-left: 20px;
}

.footer__table-headline {
  margin-bottom: 0;
}

//Ikony
.footer__icon {
  width: 30px;
}

.footer__icon.mail {
  margin-right: 5px;
  width: 25px;
}

.footer__icon.hours {
  margin-right: 5px;
  width: 23px;
}

//Ikony flex
.footer__flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer p {
  margin: 0;
}

//Footer item margin
.footer__item {
  margin-bottom: 20px;
}
